import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

export default function Main() {
  return (
    <section className="main">
      <div className="main__content-panel">
        <h1>infoplus</h1>
        <BulletTitle title="Simplu" icon="check" />
        <BulletTitle title="Rapid" icon="bolt" />
        <BulletTitle title="Intuitiv" icon="lightbulb" />
      </div>
    </section>
  );
}

function BulletTitle({ title, icon }) {
  return (
    <>
      <div className="content-panel__bullet">
        <FontAwesomeIcon icon={icon} className="bullet__icon" />
      </div>
      <p>{title}</p>
    </>
  );
}
